.article {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 20px;
  background-color: #fff;
}

.content {

  background-color: #fff;
  max-width: 950px;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 32px 16px;
  text-align: left;
  border: 0.1px solid #306EE8;
  box-shadow: rgba(23, 92, 230, 0.15) 0px 4px 24px;

  @media only screen and (max-width: 768px) {
    width: 400px;
  }

}

.content {
  border-radius: 10px;
}

.custom-background-image {
  width: 100%;
  height: auto;
}

.component-style {
  margin-top: 150px;
}